.input-profile {
  border-bottom: 1px solid #eaeaea;
  margin-bottom: 10px;
  padding-bottom: 10px;
  list-style: none;
}

.input-profile:last-of-type {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.input-profile__label {
  position: relative;
}

.input-profile__span {
  cursor: pointer;
  color: #1e1e1e;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.input-profile__input {
  box-sizing: border-box;
  width: 100%;
  text-align: right;
  color: #999;
  border: none;
  outline: none;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  transition: color .3s;
}

.input-profile__input:focus + .input-profile__span {
  color: #3369f3;
}

